import React from "react"
const p5 = typeof window !== `undefined` ? require("p5") : null

export default class BubbleSort extends React.Component {
    constructor(props) {
      super(props);
      this.myRef = React.createRef();
    }

    componentDidMount() {
        this.myP5 = new p5(this.Sketch, this.myRef.current);
    }

    Sketch = (p) => {
        let values = [];
        let i = 0;
        
        p.setup = () => {
            p.createCanvas(600, 300);
            values = new Array(p.width);
            randomize(values);
        }
    
        p.draw = () => {
            p.background(0);
            
            if (i < values.length) {
                for (let j = 0; j < values.length - i - 1; j++) {
                    let a = values[j];
                    let b = values[j + 1];
                    if (a > b) {
                        swap(values, j, j + 1);
                    }
                }
                i++;
            } else {
                i = 0;
                values = new Array(p.width);
                randomize(values);
            }

            for (let i = 0; i < values.length; i++) {
                p.stroke(255);
                p.line(i, p.height, i, p.height - values[i])
            }
        }

        function swap(arr, a, b) {
            let temp = arr[a];
            arr[a] = arr[b];
            arr[b] = temp;
        }

        function randomize(arr) {
            for (let i = 0; i < values.length; i++) {
                values[i] = p.random(p.height);
            }
        }
    }

    render() {
        return (
            <div ref={this.myRef}>
    
            </div>
        )
    }
}